.App {
  padding: 20px;
  margin: 100px 20px 100px 20px;
  box-shadow: 0 0 20px 3px #e6e6e6;
  max-width: 100%;
}

div {
  display: block;
}

.table {
  text-align: center;
  width: 100%;
}

.table-header {
  text-align: left;
  padding-left: 15px;
}

.table th,
.table td{
  vertical-align: 0;
  padding: .75rem;
  border-top: 3px solid #f6f6f6;
}

.btn-counter {
  margin: 5px;
  font-weight: bold;
}

.bottom-row {
  display: flex;
}

.col {
  flex: 0 0 50%;
  max-width: 50%;
}

.btn-toolbar {
  display: flex;
  margin-top: 40px;
  margin-bottom: 20px;
  justify-content: flex-end;
}

.btn-primary {
  margin-left: 10px;
  margin-right: 10px;
}